.calculator {

  width: 300px;
  margin: 0 auto;
  background-color: black;
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: right;
  margin-top: 30px;
}
h2{
     font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
     
}
.display {
  font-size: 24px;
  margin-bottom: 20px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

button {
  background-color: #444;
  color: white;
  font-size: 24px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #555;
}

button:active {
  transform: scale(0.95);
}
